.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 10;
}

.logo img{
    width: 170px;
    height: 90px;
    margin-left: 10px;
}

.links ul{
    list-style-type: none;
    display: flex;
}

.links ul li{
    padding: 9px 20px;
    font-size: 15px;
    margin: 0 15px;
    border: 2px solid rgb(255,152,0);
    border-radius: 25px;
    cursor: pointer;
    transition: all ease 0.3s;
}

.links ul li:hover{
    border-color: rgb(3,169,244);
}

@media screen and (max-width: 600px) {
    .links ul li{
        margin: 0 10px;
    }
    .logo img{
        width: 75px;
        height: 70px;
        margin: 10px;
    }
}